:root {
  --primary-color: #188bc4;
  --second-color: #6c6c6c;
  --third-color: #e5e5e5;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #ddd;
  background-color: #202020;
  background-image: url("background.5e438c75.jpg");
  background-size: cover;
  background-attachment: fixed;
  font-family: -a pple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

.nave-bar {
  height: 2rem;
  margin-left: 5rem;
  display: flex;
}

.link {
  color: #eee;
  margin-right: 2rem;
  text-decoration: none;
}

.bio {
  margin-top: 20%;
  font-size: 2rem;
}

p.bio {
  color: #ddd;
}

.skills {
  flex-direction: column;
  margin-top: 5rem;
  display: flex;
}

.skills-card {
  text-align: left;
  color: var(--second-color);
  flex-direction: column;
  display: flex;
}

.skills-card-groub {
  color: var(--third-color);
  justify-content: start;
  margin: 0% 10%;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.skills-card-groub > li {
  margin-right: 1rem;
}

.shadow {
  border: 1px solid var(--second-color);
  border-radius: 10px;
  padding: 0 1rem .25rem;
  transition: box-shadow .3s;
}

.shadow:hover {
  box-shadow: 0 0 11px var(--second-color);
}

.projects {
  flex-direction: row;
  justify-content: space-around;
  display: flex;
}

.project-brief {
  width: 20%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  flex-direction: column;
  display: flex;
}

.refrences {
  justify-content: center;
  display: flex;
}

.refrences > button {
  border-style: none;
  border: 1px solid var(--second-color);
  color: var(--third-color);
  background-color: #0000;
  border-radius: 5px;
  margin-left: .2rem;
  display: flex;
}

.refrences > button > img {
  width: 2rem;
}

.refrences > button > label {
  margin-top: .3rem;
  font-size: small;
}

.refrences > button:hover {
  box-shadow: 0 0 11px var(--second-color);
  cursor: pointer;
}

.label-hover:hover {
  cursor: pointer;
}

.project-details {
  width: 40rem;
  height: 30rem;
  border: 1px solid var(--second-color);
  box-shadow: 0 0 11px var(--second-color);
  background-image: url("background.5e438c75.jpg");
  background-size: cover;
  background-attachment: fixed;
  border-radius: 5px;
  margin-left: 20%;
  display: none;
  position: absolute;
}

.project-details:hover {
  cursor: pointer;
}

.show-details {
  display: flex;
}

.hide-details {
  display: none;
}

#project-details-exit-icon {
  width: 3rem;
  height: 3rem;
  margin-left: 92%;
  position: absolute;
}

