:root {
    --primary-color: #188bc4;
    --second-color: #6c6c6c;
    --third-color: #e5e5e5;
}

body {
    background-color: #202020;
    font-family: -a pple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-image: url(./Assets/background.jpg);
    background-size: cover;
    background-attachment: fixed;

    color: #dddddd;
}

.nave-bar {
    display: flex;
    height: 2rem;
    margin-left: 5rem;
}

.link {
    color: #eeeeee;
    margin-right: 2rem;
    text-decoration: none;
}

.bio {
    margin-top: 20%;
    font-size: 2rem;
}

p.bio {
    color: #dddddd;
}

.skills {
    display: flex;
    flex-direction: column;
    margin-top: 5rem;
}

.skills-card {
    display: flex;
    flex-direction: column;
    text-align: left;
    color: var(--second-color);
}

.skills-card-groub{
    display: flex;
    list-style-type: none;
    justify-content: start;
    margin: 0% 10%;
    padding: 0px;
    color: var(--third-color);
}

.skills-card-groub > li {
    margin-right: 1rem;
}


.shadow {
    transition: box-shadow .3s;
    border-radius:10px;
    border: 1px solid var(--second-color);
    padding: 0 1rem;
    padding-bottom: 0.25rem;

}

.shadow:hover {
    box-shadow: 0 0 11px var(--second-color); 
  }

.projects {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.project-brief {
    display: flex;
    flex-direction: column;
    width: 20%;
    height: fit-content;
    /* padding: 0 1rem; */
}

.refrences {
    display: flex;
    justify-content: center;
}
.refrences > button {
    background-color: transparent;
    border-style: none;
    display: flex;
    border: 1px solid var(--second-color);
    border-radius: 5px;
    color: var(--third-color);
    margin-left: 0.20rem;
}

.refrences > button > img {
    width: 2rem;
}

.refrences > button > label {
    margin-top: 0.30rem;
    font-size: small;
}

.refrences > button:hover {
    box-shadow: 0 0 11px var(--second-color); 
    cursor: pointer;
}

.label-hover:hover {
    cursor: pointer;
}

.project-details {
    display: none;
    width: 40rem;
    height: 30rem;
    border: 1px solid var(--second-color);
    border-radius: 5px;
    position: absolute;
    box-shadow: 0 0 11px var(--second-color);
    background-image: url(./Assets/background.jpg);
    background-size: cover;
    background-attachment: fixed;
    margin-left: 20%;
}

.project-details:hover {
    cursor: pointer;
}

.show-details {
    display: flex
}
.hide-details {
    display: none;
}

#project-details-exit-icon {
    width: 3rem;
    height: 3rem;
    margin-left: 92%;
    position: absolute;
}